footer {
  background-color: #0000005d;
  /* Change the background color as needed */
  color: #fff;
  /* Change the text color as needed */
  padding: 20px 0;
  /* Adjust padding as needed */
  text-align: center;
  position: relative;
  bottom: 0;
  width: 100%;
}

footer p {
  margin: 0;
}

@media (max-width: 768px) {

  .hero-content,
  .about-me-content {
    flex-direction: column;
  }

  .hero-text,
  .hero-image,
  .about-me-text,
  .about-me-image {
    max-width: 100%;
  }
}

.project-section {
  display: flex;
  justify-content: space-between;
  /* Ensures two columns on larger screens */
  margin-bottom: 30px;
}

.project-content {
  width: calc(50% - 15px);
  /* Adjusted width for two columns */
  margin-bottom: 15px;
  /* Optional: Adjust vertical spacing */
}

@media (max-width: 768px) {
  .project-section {
    flex-direction: column;
    /* Stacks projects vertically on smaller screens */
  }

  .project-content {
    width: 100%;
    /* Full width for projects on mobile */
  }
}

@media (max-width: 600px) {
  body {
    font-size: 14px; /* Decrease font size for mobile devices */
  }
}

/* If you need to adjust specific text elements */
@media (max-width: 600px) {
  h1 {
    font-size: 2em; /* Adjust heading sizes */
  }
  
  h2 {
    font-size: 1.4em;
  }
  
  p {
    font-size: 0.9em;
  }

  /* Adjust other elements as needed */
  .project .overlay,
  .project .text {
    font-size: 0.8em;
  }
}